import { Avatar, Grid,Paper, Typography } from '@material-ui/core';
import React from 'react';
import styles from './Skills.module.css';
import { makeStyles } from '@material-ui/core/styles';
import react_image from './logo192.png'



const useStyles = makeStyles((theme) => ({
    root: {
        
      display: 'flex',
      '& > *': {
        margin: theme.spacing(2),
      },
    },
    
    large: {
      width: '90px',
      height: '90px',
      background: '#c4c4c4',
      margin: theme.spacing(1)
    },
    paper: {
        background:'none',
        color:'#c4c4c4',
        padding: theme.spacing(2),
        margin: theme.spacing(1),
        minWidth: '110px'
      }
  }));



export const Skills = () => {
    const classes = useStyles();
    return (
        <div className={styles.skills_Wrapper_parent}>

            <div className={styles.skills_Wrapper}>
                <div className={styles.skills_heading}>
                    <h1>Skills</h1>

                  
                </div>
            
                



     
          
          

                   
        
    <div className={styles.tobeCenter}>
      <Grid container>
         
          <Grid xs={12} sm={4} md={3} lg={2} xl={2} >

          <Paper className={classes.paper}><Avatar className={classes.large}><img src="https://img.icons8.com/color/48/000000/nodejs.png" alt="N/A"/></Avatar>
          <Typography>Node Js</Typography>

          
          </Paper>
          </Grid>
          <Grid xs={12} sm={4} md={3} lg={2} xl={2} >
          <Paper className={classes.paper}><Avatar className={classes.large}><img src="https://img.icons8.com/color/48/000000/javascript--v1.png" alt="N/A"/></Avatar>
          <Typography>JavaScript</Typography>
          </Paper>
          </Grid>
          <Grid xs={12} sm={4} md={3} lg={2} xl={2} >
           <Paper className={classes.paper}><Avatar className={classes.large}><img src="https://img.icons8.com/color/48/000000/html-5.png" alt="N/A"/></Avatar>
           <Typography>HTML5</Typography>
           </Paper>
          </Grid>
          <Grid xs={12} sm={4} md={3} lg={2} xl={2} >
          <Paper className={classes.paper}><Avatar className={classes.large}><img src="https://img.icons8.com/color/48/000000/css3.png" alt="N/A"/></Avatar>
          <Typography>CSS3</Typography>
          </Paper>
          </Grid>
          <Grid xs={12} sm={4} md={3} lg={2} xl={2} >
           <Paper className={classes.paper}><Avatar className={classes.large}><img src="https://img.icons8.com/ios-filled/50/000000/stripe.png" alt="N/A"/></Avatar>
           <Typography>Stripe</Typography>
           </Paper>
          </Grid>
          <Grid xs={12} sm={4} md={3} lg={2} xl={2} >
          <Paper className={classes.paper}><Avatar className={classes.large}><img src={react_image} alt="N/A" width="80%"/></Avatar>
          <Typography>React</Typography>
          </Paper>
          </Grid>
                   
                    
          <Grid xs={12} sm={4} md={3} lg={2} xl={2} >
          <Paper className={classes.paper}><Avatar className={classes.large}><img src="https://img.icons8.com/color/48/000000/heroku.png" alt="N/A"/></Avatar>
          <Typography>Heroku</Typography>
          </Paper> 
          </Grid>                 
                    
          <Grid xs={12} sm={4} md={3} lg={2} xl={2} >
           <Paper className={classes.paper}><Avatar className={classes.large}><img src="https://img.icons8.com/color/48/000000/redux.png" alt="N/A"/></Avatar>
           <Typography>Redux</Typography>
           </Paper>
          </Grid>
          <Grid xs={12} sm={4} md={3} lg={2} xl={2} >
          <Paper className={classes.paper}><Avatar className={classes.large}><img src="https://img.icons8.com/ios-filled/50/000000/mysql.png" alt="N/A"/></Avatar>
          <Typography>Mysql</Typography>
          </Paper>
          </Grid>
          <Grid xs={12} sm={4} md={3} lg={2} xl={2} >
          <Paper className={classes.paper}><Avatar className={classes.large}><img src="https://img.icons8.com/color/50/000000/mongodb.png" alt="N/A"/></Avatar>
          <Typography>MongoDB</Typography>
          </Paper>
          </Grid>
          <Grid xs={12} sm={4} md={3} lg={2} xl={2} >
          <Paper className={classes.paper}><Avatar className={classes.large}><img src="https://img.icons8.com/color/48/000000/material-ui.png" alt="N/A"/></Avatar>
          <Typography>Material UI</Typography>
          </Paper>
          </Grid>
          <Grid xs={12} sm={4} md={3} lg={2} xl={2} >
          <Paper className={classes.paper}><Avatar className={classes.large}><img src="https://img.icons8.com/color/48/000000/git.png" alt="N/A"/></Avatar>
          <Typography>GIT</Typography>
          </Paper>
          </Grid>
          <Grid xs={12} sm={4} md={3} lg={2} xl={2} >
          <Paper className={classes.paper}><Avatar className={classes.large}><img src="https://img.icons8.com/color/48/000000/python--v1.png" alt="N/A"/></Avatar>
          <Typography>Python 3</Typography>
          </Paper>
          </Grid>
          <Grid xs={12} sm={4} md={3} lg={2} xl={2} >
          
                    <Paper className={classes.paper}><Avatar className={classes.large}><img src="https://img.icons8.com/color/48/000000/postgreesql.png" alt="N/A"/></Avatar>
                    <Typography>Postgresql</Typography>
                    </Paper>
          </Grid>
                   
                    
                    
                    
                    
                    
     
      </Grid>
      </div>
            </div>
            

        </div>
    )
}
