import React from 'react';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import ButtonAppBar from '../AppBar/AppBarS';

import styles from './MediaQuery.module.css'
import { AnimatedSummary } from '../AnimatedSummary/AnimatedSummary';
import { ContactForm } from '../Contact/ContactForm';
import { Skills } from '../Skills/Skills';
import GitHubCalendar from 'react-github-calendar';

export default function SimpleMediaQuery() {
  const small = useMediaQuery('(max-width:1024px)')
  const large =useMediaQuery('(min-width:1025px)')
  const exampleTheme = {
    background: 'transparent',
  
    grade4: 'hsl(338, 78%, 30%)',
    grade3: 'hsl(338, 78%, 44%)',
    grade2: 'hsl(338, 78%, 58%)',
    grade1: 'hsl(338, 78%, 72%)',
    grade0: 'rgba(27,27,27,.91)',
    text: '#c4c4c4'
  };

  return small?
  <div>
      <ButtonAppBar/>
      <AnimatedSummary/>
      <Skills/>
      <ContactForm/>
      
      
     
  </div>
  :large?
  <div className={styles.mediawrapper}>
      <ButtonAppBar home={"Home"}/>
      <AnimatedSummary home={"Home"}/>
      <Skills/>
      <GitHubCalendar username="kamalgupta97" blockSize={15} theme={exampleTheme}/>
      {/* <GitHubCalendar username="grubersjoe" theme={exampleTheme}>
  <ReactTooltip delayShow={50} html />
</GitHubCalendar> */}
      <ContactForm/>
     


      
      
        
    


     
     
  </div>
  :
  <div>

  </div>
}
